*{padding:0;margin:0;border:0}*,*:before,*:after{box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}html,body{margin:0;padding:0;height:100%;width:100%;line-height:1;font-size:14px;font-family:"Roboto Condesed sans-serif";-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%;-webkit-box-sizing:border-box}input,button,textarea{font-family:inherit}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:400}.app__wrapper{width:100vw;min-height:100vh;height:100%;display:grid;grid-template-rows:75px 1fr}.content__wrapper{position:relative;width:80%;min-height:calc(100vh - 75px);height:100%;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);font-family:"Raleway", sans-serif}.nav{height:100%;max-height:75px;display:grid;grid-template-columns:3fr 9fr;text-align:center;-webkit-align-items:center;align-items:center;font-family:"Montserrat Subrayada", sans-serif}.nav a{font-size:1.2rem}.nav__logo{height:100%}.nav__logo-img{height:100%;width:75px}.nav__menu li:hover{-webkit-animation:shake 1s ease-in-out;animation:shake 1s ease-in-out}@-webkit-keyframes shake{0%,50%,100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}10%,30%{-webkit-transform:rotate(-10deg);transform:rotate(-10deg)}20%,40%{-webkit-transform:rotate(10deg);transform:rotate(10deg)}}@keyframes shake{0%,50%,100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}10%,30%{-webkit-transform:rotate(-10deg);transform:rotate(-10deg)}20%,40%{-webkit-transform:rotate(10deg);transform:rotate(10deg)}}.preloader{position:absolute;top:50%;left:50%;-webkit-transform:translateX(-50%) translateY(-50%);transform:translateX(-50%) translateY(-50%);width:100px;z-index:999}.favorite{width:100%;display:grid;grid-gap:20px;